.form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 28px;
  box-sizing: border-box;

  margin-top: 28px;
  width: 100%;
}

.input {
  box-sizing: border-box;
  
  background-color: var(--input-back-color);
  border: {
    color: var(--on-element-color);
    width: 2px;
    style: solid;
  }

  width: 100%;
  height: 2rem;

  &_valid {
    border-color: var(--second-on-color);
  } 

  &_invalid {
    border-color: var(--error-color);
  }
}

.error_message {
  color: rgb(244, 28, 78);
}


@supports not (selector(:where)) {
  :where(input, button, a) {
    &:focus-visible {
      outline: 2px solid white;
      border-radius: 4px;
    }
  }
}