.form_btn {
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;

  background-color: var(--on-element-color);
  color: var(--element-color);
  border: none;
  border-radius: 20px;
  cursor: pointer;

  margin: 0 auto;
  padding: 0 20px;
  min-width: 240px;
  height: 40px;

  font-weight: 800;
}